document.addEventListener('DOMContentLoaded', function() {

    let h_header = document.getElementsByTagName('header')[0].offsetHeight;
    let height = document.documentElement.clientHeight;
    let trueHeight = height - h_header;
    console.log(h_header);
    console.log(height);
    console.log(trueHeight);

    if (document.documentElement.clientHeight > 600) {

        document.querySelector(".heigh-full").style.height = trueHeight + "px";

    };


});